@import url('/fonts/inter.css');

:root {
  --font--ui: Inter, 'Open Sans', 'Noto Sans', 'Roboto', 'Droid Sans', sans-serif;
  --font--mono: monospace;
}

body {
  font-family: var(--font--ui);
  font-size: 5.625vw;
  font-weight: 400;
  word-spacing: -0.05em;

  @media (min-width: 320px) {
    font-size: 18px;
  }

  @media (min-width: 1024px) {
    font-size: 20px;
  }

  @media print {
    font-size: 14px;
  }
}

strong,
b {
  font-weight: 700;
}

a {
  color: $color-accent;
  opacity: 1;
  text-decoration-skip: ink;
  transition: opacity 250ms ease;

  &:hover,
  &:focus {
    opacity: 0.8;
  }
}

code {
  font-family: var(--font--mono);
}

span.hex {
  font-family: var(--font--mono);
  &:before {
    content: '#';
  }
}

